/* eslint-disable react/prop-types */
import React, { useState } from "react";

import Footer from "../../components/Footer/Footer";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";

import { SERVER_USER_LOG_IN, WORKSPACES_URL } from "../../common/Url";
import axios from "axios";
import { FormattedMessage, injectIntl } from "react-intl";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    background: "#FFD166",
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    background: "#354868",
  },
}));

const SignIn = ({ intl }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  function validateForm() {
    return email.length > 0 && password.length > 0;
  }

  async function handleSubmit(event) {
    event.preventDefault();
    await axios
      .post(SERVER_USER_LOG_IN, {
        username: email,
        password: password,
      })
      .then((res) => {
        if (res.data.access_token) {
          console.log("Token stored");
          localStorage.setItem("token", res.data.access_token);
          window.location.href = WORKSPACES_URL;
        } else {
          setErrorMessage(<FormattedMessage id="login.serverError" />);
        }
      })
      .catch((error) => {
        console.log(error);
        setErrorMessage(
          <FormattedMessage id="login.validateEmailandPassword" />
        );
      });
  }

  const classes = useStyles();

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <AccountCircleIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          <FormattedMessage id="landing.signIn" />
        </Typography>
        <form className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label={intl.formatMessage({
              id: "login.email",
            })}
            name="email"
            autoComplete="email"
            autoFocus
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label={intl.formatMessage({
              id: "login.password",
            })}
            type="password"
            id="password"
            autoComplete="current-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />

          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={handleSubmit}
            disabled={!validateForm()}
          >
            <FormattedMessage id="landing.signIn" />
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <p style={{ color: "red" }}>{errorMessage}</p>
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                <FormattedMessage id="login.forgotPassword" />
              </Link>
            </Grid>
            <Grid item>
              <Link href="/signup" variant="body2">
                {<FormattedMessage id="login.dontHaveAccount" />}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Footer />
    </Container>
  );
};

export default injectIntl(SignIn);
