/* eslint-disable react/prop-types */
import React, { Component } from "react";
import classes from "./Toolbar.module.css";

import { FormControl, InputLabel, Select, MenuItem } from "@material-ui/core";

import UserMenu from "../UserMenu";
import { connect } from "react-redux";
import * as actionTypes from "../../../store/actions";
import detectBrowserLanguage from "detect-browser-language";

// eslint-disable-next-line no-unused-vars
class Toolbar extends Component {
  constructor(props) {
    super(props);
    props.updateCurrentLanguage(detectBrowserLanguage());
  }

  changeLan(lan) {
    this.props.updateCurrentLanguage(lan);
  }

  logOut() {
    localStorage.removeItem("token");
    window.location.href = "/";
  }

  render() {
    return (
      <header className={classes.Toolbar}>
        <div>Waterflow for {this.props.rolename}</div>

        <div>
          {/* Language select form */}
          <FormControl
            className={classes.formControl}
            style={{ marginRight: 20 }}
          >
            <InputLabel
              shrink
              style={{
                display: "inline",
                position: "absolute",
              }}
              htmlFor="name-multiple"
            >
              Language
            </InputLabel>
            <Select labelId="demo-simple-select-label" id="demo-simple-select">
              <MenuItem onClick={() => this.changeLan("zh")} value={"zh"}>
                中文
              </MenuItem>
              <MenuItem onClick={() => this.changeLan("en")} value={"en"}>
                English
              </MenuItem>
            </Select>
          </FormControl>
          {/* User menu form */}
          <FormControl className={classes.formControl}>
            <UserMenu />
          </FormControl>
        </div>
      </header>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    curLan: state.currentLanguage,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentLanguage: (currentLan) =>
      dispatch({ type: actionTypes.UPDATE_LANGUAGE, data: currentLan }),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Toolbar);
