/* eslint-disable react/prop-types */
import React from "react";

import BpmnJS from "bpmn-js/dist/bpmn-navigated-viewer.production.min.js";
import "./TasksStatusColor.css";
import { TaskStatus } from "../../common/TaskStatus";

export default class ReactBpmn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.containerRef = React.createRef();
  }

  componentDidMount() {
    const { diagramXML, tasks } = this.props;

    const container = this.containerRef.current;

    this.bpmnViewer = new BpmnJS({ container: container, height: 600 });

    this.bpmnViewer.on("import.done", () => {
      this.bpmnViewer.get("canvas").zoom("fit-viewport");
    });

    if (diagramXML) {
      this.displayDiagram(diagramXML, tasks);
    }
  }

  componentWillUnmount() {
    this.bpmnViewer.destroy();
  }

  async displayDiagram(diagramXML, tasks) {
    await this.bpmnViewer.importXML(diagramXML);
    if (tasks) {
      var canvas = this.bpmnViewer.get("canvas");
      tasks.forEach((task) => {
        if (task.status === TaskStatus.NEW) {
          canvas.addMarker(task.bpmnTaskId, "newTask");
        } else if (task.status === TaskStatus.PASSED) {
          canvas.addMarker(task.bpmnTaskId, "passedTask");
        } else if (task.status === TaskStatus.FAILED) {
          canvas.addMarker(task.bpmnTaskId, "failedTask");
        }
      });
    }
  }

  render() {
    return (
      <div
        className="react-bpmn-diagram-container"
        ref={this.containerRef}
      ></div>
    );
  }
}
