/* eslint-disable react/prop-types */
import React, { Component } from "react";

import classes from "./Details.module.css";
import Layout from "../../components/Layout/Layout";
import Footer from "../../components/Footer/Footer";
import Menu from "../../components/Menu/MenuBar";
import { DEVELOPER_SERVER_URL } from "../../common/Url";

import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Link from "@material-ui/core/Link";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";

import axios from "axios";
import { connect } from "react-redux";
import * as actionTypes from "../../store/actions";
import { FormattedMessage, injectIntl } from "react-intl";

class Settings extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: props.id,
      bpmnTaskId: props.bpmnTaskId,
      workflowId: props.workflowId,
      name: props.name,
      developerId: props.developerId,
      ecrUri: props.ecrUri,
      ecrImage: props.ecrImage,
      ecrMemory: props.ecrMemory,
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  /**
   * Handle event when TextField input is changed
   * @param {*} event
   * @returns
   */
  handleChange(event) {
    const value = event.target.value;
    this.setState({
      ...this.state,
      [event.target.name]: value,
    });
  }

  /**
   * Handle event when submit button is clicked
   * @param {*} event
   * @returns
   */
  handleSubmit(event) {
    axios
      .put(
        DEVELOPER_SERVER_URL +
          this.state.developerId +
          "/" +
          this.state.id +
          "/configuration",
        {
          method: "PUT",
          headers: {
            Accept: "application/json, text/plain",
            "Content-Type": "application/json;charset=UTF-8",
          },
          // convert the React state to JSON and send it as the PUT body
          body: JSON.stringify(this.state),
        }
      )
      .then((response) => response.text())
      .then((data) => console.log(data))
      .catch((error) => console.log("Error detected: " + error));

    event.preventDefault();
    alert("ECR config was submitted: \n" + JSON.stringify(this.state));
  }

  render() {
    return (
      <Layout rolename="developer">
        <Grid
          style={{
            marginTop: 20,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          <div className={classes.Title}>
            <Grid
              style={{
                textAlign: "center",
              }}
              item
              xs
              container
              direction="column"
              spacing={4}
            >
              <Grid item xs>
                <Typography gutterBottom variant="h4" color="initial">
                  <FormattedMessage id="developer.task.name" />
                  {this.props.curtask.name}
                </Typography>
                <Typography variant="h6" color="textSecondary">
                  <FormattedMessage id="developer.task.bpmnid" />
                  {this.props.curtask.bpmnTaskId}
                </Typography>
              </Grid>
            </Grid>
          </div>

          <Grid
            direction="row"
            justify="space-between"
            alignItems="flex-start"
            spacing={0}
            style={{
              marginTop: 40,
            }}
            container
            item
            xs={12}
          >
            <Grid
              style={{
                height: 260,
                background: "#354868",
                borderRadius: 10,
              }}
              item
              xs={2.5}
            >
              <Menu className={classes.Menu} />
            </Grid>

            <Grid
              style={{
                height: 600,
                background: "#5D5D5B",
                borderRadius: 10,
                color: "#5D5D5B",
              }}
              item
              xs={9}
            >
              <Grid
                style={{
                  marginTop: 20,
                  marginLeft: 20,
                  marginRight: 20,
                  height: 530,
                  background: "#C4C4C4",
                  borderColor: "white",
                  border: 0,
                  borderRadius: 10,
                  overflow: "scroll",
                }}
                item
                xs={12}
              >
                {/* <div className={classes.SubTitle}>ECR Config </div> */}
                <div className={classes.SubTitle}>
                  <FormattedMessage id="developer.task.settings.config" />{" "}
                </div>
                <form onSubmit={this.handleSubmit}>
                  <div className={classes.MainArea}>
                    <div className={classes.TextArea}>
                      <TextField
                        id="outlined-full-width-uri"
                        label={this.props.intl.formatMessage({
                          id: "developer.task.settings.imageUri",
                        })}
                        name="ecrUri"
                        style={{ margin: 8 }}
                        placeholder="Text"
                        fullWidth
                        margin="normal"
                        InputLabelProps={{
                          shrink: true,
                        }}
                        variant="outlined"
                        value={this.state.ecrUri}
                        onChange={this.handleChange}
                      />
                      <TextField
                        id="outlined-full-width-image-tag"
                        label={this.props.intl.formatMessage({
                          id: "developer.task.settings.imageTag",
                        })}
                        name="ecrImage"
                        style={{ margin: 8 }}
                        placeholder="Text"
                        fullWidth
                        margin="normal"
                        InputLabelProps={{
                          shrink: true,
                        }}
                        variant="outlined"
                        value={this.state.ecrImage}
                        onChange={this.handleChange}
                      />
                      <TextField
                        id="outlined-full-width-memory"
                        label={this.props.intl.formatMessage({
                          id: "developer.task.settings.memory",
                        })}
                        name="ecrMemory"
                        style={{ margin: 8 }}
                        placeholder="256"
                        fullWidth
                        margin="normal"
                        InputLabelProps={{
                          shrink: true,
                        }}
                        variant="outlined"
                        value={this.state.ecrMemory}
                        onChange={this.handleChange}
                      />
                    </div>
                  </div>

                  <div className={classes.Button}>
                    <Button
                      variant="contained"
                      startIcon={<CloudUploadIcon />}
                      type="submit"
                      value="Submit"
                    >
                      <FormattedMessage id="developer.task.settings.submit" />
                    </Button>
                  </div>
                </form>

                <div className={classes.SubTitle}>
                  <FormattedMessage id="developer.task.settings.reference" />
                </div>
                <div className={classes.Link}>
                  <Typography>
                    <Link
                      href="https://aws.amazon.com/premiumsupport/knowledge-center/secondary-account-access-ecr/"
                      onClick={() => {
                        console.info("I'm reference button.");
                      }}
                    >
                      <FormattedMessage id="developer.task.settings.grantAccess" />
                    </Link>
                  </Typography>
                </div>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        {/* footer */}
        <Footer />
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    curtask: state.currentTask,
    id: state.currentTask.id,
    bpmnTaskId: state.currentTask.bpmnTaskId,
    workflowId: state.currentTask.workflowId,
    name: state.currentTask.name,
    developerId: state.currentTask.developerId,
    ecrUri: state.currentTask.ecrUri,
    ecrImage: state.currentTask.ecrImage,
    ecrMemory: state.currentTask.ecrMemory,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentTask: (currentTask) =>
      dispatch({ type: actionTypes.UPDATE_CURRENT_TASK, data: currentTask }),
    updateAllTasks: (allTask) =>
      dispatch({ type: actionTypes.UPDATE_ALL_NEWTASKS, data: allTask }),
  };
};

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(Settings)
);
