/* eslint-disable react/prop-types */
import React, { Component } from "react";

import classes from "./Workspaces.module.css";
import Workspace from "../../components/Workspace/WorkspaceBar";
import Layout from "../../components/Layout/Layout";
import Footer from "../../components/Footer/Footer";
import {
  SERVER_USER_WORKSPACES,
  ADMIN_ADD_MEMBER,
  DEVELOPER_ALLTASKS_URL,
  DESIGNER_URL,
} from "../../common/Url";
import { UserRoles } from "../../common/UserRoles";
import Modal from "../../components/Modal/Modal";

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import SearchIcon from "@material-ui/icons/Search";
import Button from "@material-ui/core/Button";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import MenuItem from "@material-ui/core/MenuItem";

import axios from "axios";
import { connect } from "react-redux";
import * as actionTypes from "../../store/actions";
import { FormattedMessage, injectIntl } from "react-intl";

class Workspaces extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currWorkspaces: [],
      filter: "",
      addNewWorkspace: false,
      newNewWorkspace: {
        name: "",
        role: "",
      },
      curList: [{ name: "", role: "" }],
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleRoleChange = this.handleRoleChange.bind(this);
    this.closeAddNewWorkspaceModal = this.closeAddNewWorkspaceModal.bind(this);
    this.updateNewWorkspace = this.updateNewWorkspace.bind(this);
  }

  componentDidMount() {
    const url = SERVER_USER_WORKSPACES;
    axios
      .get(url)
      .then((response) => response.data)
      .then((data) => {
        this.props.updateAllWorkspaces(data);
        console.log(data);
        console.log("##############################check state ######");
        console.log(this.props.currWorkspaces);
      });
  }

  handleChange(event) {
    const value = event.target.value;
    this.setState({
      ...this.state,
      [event.target.name]: value,
      newNewWorkspace: {
        role: this.state.newNewWorkspace.role,
        name: event.target.value,
      },
    });
  }

  handleRoleChange(role) {
    this.setState({
      newNewWorkspace: {
        role: role,
        name: this.state.newNewWorkspace.name,
      },
    });
  }

  directToWorkspace(data) {
    this.props.updateCurrentWorkspace(data);
    // redirect to admin, developer or designer page accordingly
    if (data.role === UserRoles.ADMIN)
      this.props.history.push(ADMIN_ADD_MEMBER);
    if (data.role === UserRoles.DESIGNER) this.props.history.push(DESIGNER_URL);
    if (data.role === UserRoles.DEVELOPER)
      this.props.history.push(DEVELOPER_ALLTASKS_URL);
  }

  showAddNewWorkspaceModal() {
    this.setState({ addNewWorkspace: true });
  }

  closeAddNewWorkspaceModal() {
    this.setState({ addNewWorkspace: false });
  }

  updateNewWorkspace() {
    this.setState({
      addNewWorkspace: false,
      curList: [...this.state.curList, this.state.newNewWorkspace],
    });
  }

  render() {
    return (
      <Layout rolename="workspaces">
        <Grid
          style={{
            marginTop: 80,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          {/* title area */}
          <div className={classes.TitleAndSearch}>
            <div className={classes.Title}>
              <FormattedMessage id="workspaces.allWorkspaces"></FormattedMessage>
            </div>
          </div>

          {/* button and search area */}
          <Grid
            container
            direction="row"
            justify="space-between"
            spacing={0}
            item
            xs={12}
          >
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={4}
            >
              <Button
                variant="contained"
                startIcon={<AddCircleIcon />}
                type="submit"
                value="Submit"
                onClick={() => {
                  this.showAddNewWorkspaceModal();
                }}
              >
                <FormattedMessage id="workspaces.create"></FormattedMessage>
              </Button>
            </Grid>
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={6}
            >
              <TextField
                style={{
                  display: "flex",
                  borderColor: "white",
                  border: 0,
                  borderRadius: 3,
                  backgroundColor: "white",
                  paddingLeft: 10,
                  paddingRight: 10,
                }}
                placeholder={this.props.intl.formatMessage({
                  id: "workspaces.search",
                })}
                variant="standard"
                name="filter"
                value={this.state.filter}
                onChange={this.handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment>
                      <IconButton>
                        <SearchIcon />
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
          </Grid>

          {/* workspaces area */}
          <Grid
            container
            direction="row"
            justify="space-evenly"
            spacing={0}
            style={{
              height: 700,
              background: "#5D5D5B",
              borderRadius: 10,
              color: "#5D5D5B",
            }}
            item
            xs={12}
          >
            {/* admin workspace area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.admin.workspaces"></FormattedMessage>
              </div>
              {this.props.currWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.ADMIN &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
              {/* created new admin workspace */}
              {this.state.curList.map((item) => {
                if (item.role === UserRoles.ADMIN)
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
            {/* designer workspaces area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.designer.workspaces"></FormattedMessage>
              </div>
              {this.props.currWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.DESIGNER &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
              {/* created new designer workspace */}
              {this.state.curList.map((item) => {
                if (item.role === UserRoles.DESIGNER)
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
            {/* developer workspaces area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                marginRight: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.WorkspaceTitle}>
                <FormattedMessage id="workspaces.developer.workspaces"></FormattedMessage>
              </div>
              {this.props.currWorkspaces.map((item) => {
                if (
                  item.role === UserRoles.DEVELOPER &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
              {/* created new developer workspace */}
              {this.state.curList.map((item) => {
                if (item.role === UserRoles.DEVELOPER)
                  return (
                    <Workspace
                      key={item.id}
                      workspacename={item.name}
                      clicked={() => this.directToWorkspace(item)}
                    />
                  );
              })}
            </Grid>
          </Grid>
        </Grid>
        {/* create workspace modal */}
        <Modal
          show={this.state.addNewWorkspace}
          modalClosed={this.closeAddNewWorkspaceModal}
        >
          <form className={classes.form} noValidate>
            <div className={classes.ModalTitle}>
              <FormattedMessage id="workspaces.workspaceInfo"></FormattedMessage>
            </div>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="name"
                  label={this.props.intl.formatMessage({
                    id: "workspaces.name",
                  })}
                  name="name"
                  value={this.state.newNewWorkspace.name}
                  onChange={this.handleChange}
                  autoComplete="name"
                />
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <TextField
                select
                fullWidth
                id="role"
                label={this.props.intl.formatMessage({
                  id: "workspaces.role",
                })}
                helperText={this.props.intl.formatMessage({
                  id: "workspaces.selectRole",
                })}
                style={{ marginTop: 10 }}
              >
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "workspaces.admin.workspaces",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.ADMIN)}
                >
                  <FormattedMessage id="workspaces.admin.workspaces"></FormattedMessage>
                </MenuItem>
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "workspaces.designer.workspaces",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.DESIGNER)}
                >
                  <FormattedMessage id="workspaces.designer.workspaces"></FormattedMessage>
                </MenuItem>
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "workspaces.developer.workspaces",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.DEVELOPER)}
                >
                  <FormattedMessage id="workspaces.developer.workspaces"></FormattedMessage>
                </MenuItem>
              </TextField>
            </Grid>
            <Button
              fullWidth
              variant="contained"
              color="primary"
              style={{
                marginTop: 10,
                marginBottom: 30,
              }}
              onClick={() => this.updateNewWorkspace()}
            >
              <FormattedMessage id="workspaces.createButton"></FormattedMessage>
            </Button>
          </form>
        </Modal>
        {/* footer */}
        <Footer />
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currWorkspaces: state.allNewWorkspaces,
    filter: state.filter,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentWorkspace: (currentWorkspace) =>
      dispatch({
        type: actionTypes.UPDATE_CURRENT_WORKSPACE,
        data: currentWorkspace,
      }),
    updateAllWorkspaces: (allNewWorkspaces) =>
      dispatch({
        type: actionTypes.UPDATE_ALL_NEWWORKSPACES,
        data: allNewWorkspaces,
      }),
  };
};

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(Workspaces)
);
