/* eslint-disable react/prop-types */
import "./App.css";
import React, { Component } from "react";

import {
  DESIGNER_URL,
  DEVELOPER_ALLTASKS_URL,
  DEVELOPER_DETAILS_URL,
  DEVELOPER_SETTINGS_URL,
  DEVELOPER_STATUS_URL,
  SIGN_UP_URL,
  SIGN_IN_URL,
  ADMIN_ADD_MEMBER,
  WORKSPACES_URL,
} from "../src/common/Url";
import WorkflowCanvas from "./containers/designer/WorkflowCanvas/WorkflowCanvas";
import AllTasks from "../src/containers/developer/AllTasks";
import Details from "../src/containers/developer/Details";
import Settings from "../src/containers/developer/Settings";
import Status from "../src/containers/developer/Status";
import SignUp from "../src/containers/login/SignUp";
import SignIn from "../src/containers/login/SignIn";
import Landing from "../src/containers/login/Landing";
import AddMember from "../src/containers/admin/AddMember/AddMember";
import Workspaces from "../src/containers/login/Workspaces";

import { Route, Switch } from "react-router-dom";
import { IntlProvider } from "react-intl";
import zh from "./i18n/zh";
import en from "./i18n/en";
import { connect } from "react-redux";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      curLan: "",
    };
  }

  /**
   * To recognize
   * @param {*} lang lang in string(like en, zh, zh_tw, etc)
   * @returns the translation set from i18n
   */
  recognizeLanguage(lang) {
    if (lang.includes("zh")) {
      return zh;
    } else {
      return en;
    }
  }

  render() {
    return (
      <IntlProvider
        locale="en"
        defaultLocale="en"
        messages={this.recognizeLanguage(this.props.curLan)}
      >
        <Switch>
          <Route path={SIGN_UP_URL} component={SignUp} />
          <Route path={SIGN_IN_URL} component={SignIn} />
          <Route path={DESIGNER_URL} exact component={WorkflowCanvas} />
          <Route path={DEVELOPER_ALLTASKS_URL} component={AllTasks} />
          <Route path={DEVELOPER_DETAILS_URL} component={Details} />
          <Route path={DEVELOPER_SETTINGS_URL} component={Settings} />
          <Route path={DEVELOPER_STATUS_URL} component={Status} />
          <Route path={SIGN_UP_URL} component={SignUp} />
          <Route path={SIGN_IN_URL} component={SignIn} />
          <Route path={ADMIN_ADD_MEMBER} component={AddMember} />
          <Route path={WORKSPACES_URL} component={Workspaces} />
          <Route path="/" component={Landing} />
        </Switch>
      </IntlProvider>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    curLan: state.currentLanguage,
  };
};
export default connect(mapStateToProps)(App);
