// React moduel
import React, { Component } from "react";

import classes from "./WorkflowCanvas.module.css";
import Layout from "../../../components/Layout/Layout";
import Footer from "../../../components/Footer/Footer";
import { BPMN_SERVER_URL, SIGN_IN_URL } from "../../../common/Url";

import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Snackbar from "@material-ui/core/Snackbar";
import Alert from "@material-ui/lab/Alert";
import SaveIcon from "@material-ui/icons/Save";

import DiagramHistoryDialog from "../../../components/DiagramHistoryDialog/DiagramHistoryDialog";
import DeployModeDialog from "../../../components/DeployModeDialog/DeployModeDialog";
import axios from "axios";

// Official Bpmn.io modeler and propertis panel moduel
import propertiesPanelModule from "bpmn-js-properties-panel";
import "bpmn-js/dist/assets/diagram-js.css";
import "bpmn-font/dist/css/bpmn-embedded.css";
import BpmnModeler from "bpmn-js/lib/Modeler";

// This project's custom bpmn xml properties descriptor and panel component
import WaterflowPropertiesPanel from "../../../components/BpmnPropertiesPanel";
import waterflowModdleDescriptor from "../../../assets/moddle-extension/waterflow.json";
import "./BpmnPropertiesPanel.css";

// Zeebe's model provider and properties panel descriptor
import zeebeModdle from "zeebe-bpmn-moddle/resources/zeebe";
import zeebePropertiesProvider from "../../../assets/moddle-extension/zeebe/custom/properties-provider/";
import { FormattedMessage } from "react-intl";

class WorkflowCanvas extends Component {
  constructor(props) {
    super(props);

    this.state = {
      bearerToken: localStorage.getItem("token"),
      workspaceId: "89600b7e-7cf0-11eb-9439-0242ac130002",
      workflowId: "89600b7e-7cf0-11eb-9439-0242ac130002",
      alertbar: {
        open: false,
        severity: "success",
        message: "",
      },
    };
    this.openBpmnDiagram = this.openBpmnDiagram.bind(this);
    this.handleAlertClose = this.handleAlertClose.bind(this);
  }

  componentDidMount() {
    if (!this.state.bearerToken) {
      window.location.href = SIGN_IN_URL;
    }

    // load BPMN canvas and official properties panel
    const $modelerContainer = document.querySelector("#bpmn-canvas");
    const $propertiesContainer = document.querySelector("#bpmn-props");
    this.loadBpmnModelerAndProperties($modelerContainer, $propertiesContainer);

    // load custom properties panel where developers can be selected
    const $developersContainer = document.querySelector("#bpmn-developers");
    this.loadCustomPropertiesPanel($developersContainer);

    // load BPMN diagram
    this.loadDiagram();
  }

  /**
   * Load BPMN diagram by fetching an endpoint
   */
  async loadDiagram() {
    const config = {
      headers: { Authorization: `Bearer ${this.state.bearerToken}` },
    };
    const serverResponse = await axios.get(
      BPMN_SERVER_URL +
        "/" +
        this.state.workspaceId +
        "/" +
        this.state.workflowId,
      config
    );

    const bpmnDiagramJson = serverResponse.data;

    if (bpmnDiagramJson && bpmnDiagramJson.diagram) {
      const decodedXml = window.atob(bpmnDiagramJson.diagram);
      console.log("Default diagram XML: ", decodedXml);
      this.openBpmnDiagram(decodedXml);
    } else {
      this.openAlertbar("error", "Cannot load the canvas");
    }
  }

  /**
   * Save BPMN diagram by fetching an endpoint
   */
  async saveDiagram() {
    try {
      const xml = await this.modeler.saveXML({
        format: true,
      });
      const currentDiagramXML = xml.xml;
      const encodedDiagram = window.btoa(currentDiagramXML);
      console.log("Current diagram XML: ", currentDiagramXML);

      const diagramObject = {
        workspaceId: this.state.workspaceId,
        workflowId: this.state.workflowId,
        diagram: encodedDiagram,
      };

      const config = {
        headers: { Authorization: `Bearer ${this.state.bearerToken}` },
      };

      axios
        .put(
          BPMN_SERVER_URL +
            "/" +
            this.state.workspaceId +
            "/" +
            this.state.workflowId,
          diagramObject,
          config
        )
        .then(() => {
          this.openAlertbar("success", "Canvas is successfully saved");
        });
    } catch (err) {
      this.openAlertbar("error", "Fail to save canvas");
      console.error("Error happened saving XML: ", err);
    }
  }

  /**
   * Load bpmn modeler to DOM
   * @param {*} container which container(may from query selector) the modeler is going to place
   */
  loadBpmnModelerAndProperties(modelContainer, propertiesContainer) {
    this.modeler = new BpmnModeler({
      container: modelContainer,
      propertiesPanel: {
        parent: propertiesContainer,
      },
      keyboard: {
        bindTo: document.body,
      },
      additionalModules: [propertiesPanelModule, zeebePropertiesProvider],
      moddleExtensions: {
        waterflow: waterflowModdleDescriptor,
        zeebe: zeebeModdle,
      },
    });
  }

  /**
   * Load custom bpmn properties panel to DOM (define in components/PropertiesPanel)
   * @param {*} container which container(may from query selector) the properties panel is going to place
   */
  loadCustomPropertiesPanel(container) {
    this.propertiesPanel = new WaterflowPropertiesPanel({
      container: container,
      modeler: this.modeler,
    });
  }

  /**
   * Open an exsiting BPMN diagram
   * @param {*} xml the xml format of bpmn diagmram
   * @param {*} tasks task list returned by backend api
   */
  async openBpmnDiagram(xml) {
    try {
      const result = await this.modeler.importXML(xml);
      const { warnings } = result;
      console.log(warnings);
    } catch (err) {
      console.log(err.message, err.warnings);
    }
  }

  /**
   * Open the alert bar
   * @param {*} severity success, warning, info, or error
   * @param {*} message the message that the alert shows
   */
  openAlertbar(severity, message) {
    this.setState({
      alertbar: {
        open: true,
        severity: severity,
        message: message,
      },
    });
  }

  /**
   * handle event when alertbar is closed
   * @param {*} event
   * @param {*} reason
   * @returns
   */
  handleAlertClose(event, reason) {
    if (reason === "clickaway") {
      return;
    }
    this.setState({ alertbar: { ...this.state.alertbar, open: false } });
  }

  render() {
    return (
      <Layout rolename="designer">
        <div className={classes.Title}>
          <Paper
            style={{
              background: "#354868",
            }}
            className={classes.paper}
          >
            <Grid item xs container direction="column" spacing={4}>
              <Grid
                style={{
                  color: "white",
                  textAlign: "center",
                }}
                item
                xs
              >
                <Typography gutterBottom variant="h4" color="initial">
                  <FormattedMessage id="designer.workspace"></FormattedMessage>
                </Typography>
                <Typography variant="body1" color="initial">
                  <FormattedMessage id="desginer.workspaceDescription"></FormattedMessage>
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </div>
        <div className={classes.Button}>
          <Grid container spacing={3}>
            <Grid item xs={3}>
              <DiagramHistoryDialog
                workflowId={this.state.workflowId}
                workspaceId={this.state.workspaceId}
                openBpmnDiagram={this.openBpmnDiagram}
              />
            </Grid>
            <Grid item xs={3}>
              <Button
                variant="contained"
                startIcon={<SaveIcon />}
                onClick={() => this.saveDiagram()}
              >
                <FormattedMessage id="desginer.saveDiagram"></FormattedMessage>
              </Button>
            </Grid>
            <Grid item xs={3}>
              <DeployModeDialog
                workflowId={this.state.workflowId}
                workspaceId={this.state.workspaceId}
              />
            </Grid>
          </Grid>
        </div>
        <div className={classes.Mainarea}>
          <div id="bpmn-canvas" className={classes.Canvas} />
          <div className={classes.CanvasConfig}>
            <div id="bpmn-props" className={classes.BpmnProps} />
            <div id="bpmn-developers" className={classes.BpmnDevelopers} />
          </div>
        </div>

        <Snackbar
          open={this.state.alertbar.open}
          autoHideDuration={6000}
          onClose={this.handleAlertClose}
        >
          <Alert
            onClose={this.handleAlertClose}
            severity={this.state.alertbar.severity}
          >
            {this.state.alertbar.message}
          </Alert>
        </Snackbar>

        {/* footer */}
        <Footer />
      </Layout>
    );
  }
}

export default WorkflowCanvas;
