import React from "react";
import PropTypes from "prop-types";
import Button from "@material-ui/core/Button";
import { Component } from "react";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import HistoryIcon from "@material-ui/icons/History";
import ListItemText from "@material-ui/core/ListItemText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import { FormattedMessage, injectIntl } from "react-intl";
import { BPMN_SERVER_URL } from "../../common/Url";
import axios from "axios";

HistoryDialog.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  history: PropTypes.array,
  workspaceId: PropTypes.string,
  workflowId: PropTypes.string,
  intl: PropTypes.object,
  openBpmnDiagram: PropTypes.func,
};

function HistoryDialog(props) {
  const {
    onClose,
    open,
    history,
    workspaceId,
    workflowId,
    intl,
    openBpmnDiagram,
  } = props;

  const handleClose = () => {
    onClose();
  };

  /**
   * When click one of the record, it reloads the bpmn modeler
   * @param {*} documentId
   */
  const handleListItemClick = async (documentId) => {
    let endopoint =
      BPMN_SERVER_URL +
      "/" +
      workspaceId +
      "/" +
      workflowId +
      "/history/" +
      documentId;

    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    const oldDiagram = await axios
      .get(endopoint, config)
      .then((res) => res.data);
    const decodedDiamgram = window.atob(oldDiagram.diagram);
    // bpmnModeler.importXML(decodedDiamgram);
    openBpmnDiagram(decodedDiamgram);
    onClose();
  };

  /**
   * translate the milliseconds to humand read format
   * @param {*} millis
   */
  const millsToDateTime = (millis) => {
    let d = new Date(millis);
    return (
      intl.formatMessage({
        id: "designer.history.saveat",
      }) +
      ` ${d.getFullYear()}-${
        d.getMonth() + 1
      }-${d.getDate()}  ${d.getHours()}:${d.getMinutes()}:${d.getSeconds()}`
    );
  };

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="simple-dialog-title"
      open={open}
    >
      <DialogTitle id="simple-dialog-title">
        <FormattedMessage id="designer.history.title" />
      </DialogTitle>
      <List>
        {history.map((diagram) => (
          <ListItem
            button
            onClick={() => handleListItemClick(diagram.documentId)}
            key={diagram.documentId}
          >
            <ListItemText primary={millsToDateTime(diagram.savedDate.millis)} />
          </ListItem>
        ))}
      </List>
    </Dialog>
  );
}

class DiagramHistoryDialog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      history: [],
      workspaceId: props.workspaceId,
      workflowId: props.workflowId,
    };
    this.handleClickOpen = this.handleClickOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  async handleClickOpen() {
    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    let endpoint =
      BPMN_SERVER_URL +
      "/" +
      this.state.workspaceId +
      "/" +
      this.state.workflowId +
      "/history";

    axios.get(endpoint, config).then((res) => {
      const historyData = res.data;
      if (historyData && historyData.history) {
        this.setState({ history: historyData.history });
        this.setState({
          open: true,
        });
      }
    });
  }

  handleClose() {
    this.setState({
      open: false,
    });
  }

  render() {
    return (
      <div>
        <Button
          variant="contained"
          startIcon={<HistoryIcon />}
          onClick={this.handleClickOpen}
        >
          <FormattedMessage
            id="designer.button.history"
            defaultMessage="View History"
          />
        </Button>
        <HistoryDialog
          open={this.state.open}
          onClose={this.handleClose}
          bpmnModeler={this.bpmnModeler}
          history={this.state.history}
          workspaceId={this.state.workspaceId}
          workflowId={this.state.workflowId}
          intl={this.props.intl}
          openBpmnDiagram={this.props.openBpmnDiagram}
        />
      </div>
    );
  }
}

DiagramHistoryDialog.propTypes = {
  openBpmnDiagram: PropTypes.func.isRequired,
  workspaceId: PropTypes.string.isRequired,
  workflowId: PropTypes.string.isRequired,
  intl: PropTypes.object,
};

export default injectIntl(DiagramHistoryDialog);
