/* eslint-disable react/prop-types */
import React from "react";
import { useState } from "react";
import Footer from "../../components/Footer/Footer";

import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import AssignmentIcon from "@material-ui/icons/Assignment";

import { SERVER_USER_SIGN_UP, SIGN_IN_URL } from "../../common/Url";
import axios from "axios";
import { validateEmail } from "../../common/RegexValidator";

import { FormattedMessage, injectIntl } from "react-intl";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    background: "#FFD166",
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    background: "#354868",
  },
}));

const SignUp = ({ intl }) => {
  const classes = useStyles();

  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [alertMessage, setAlertMessage] = useState("");

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleFirstnameChange = (e) => {
    setFirstname(e.target.value);
  };

  const handleLastnameChange = (e) => {
    setLastname(e.target.value);
  };

  const handleSignUp = () => {
    if (!validateEmail(email)) {
      setAlertMessage(<FormattedMessage id="login.validateEmail" />);
      return;
    }

    if (password.length < 6) {
      setAlertMessage(<FormattedMessage id="login.validPassword" />);
      return;
    }

    if (firstname.length == 0) {
      setAlertMessage(<FormattedMessage id="login.validFirstname" />);
      return;
    }

    if (lastname.length == 0) {
      setAlertMessage(<FormattedMessage id="login.validLastname" />);
      return;
    }

    const registerData = {
      email: email,
      username: firstname + " " + lastname,
      password: password,
    };

    axios
      .post(SERVER_USER_SIGN_UP, registerData)
      .then((res) => {
        console.log(res);
        window.location.href = SIGN_IN_URL;
      })
      .catch((err) => {
        if (err.response) {
          if (err.response.status == 409) {
            setAlertMessage(<FormattedMessage id="login.existedEmail" />);
          } else {
            setAlertMessage(err.response.data);
          }
        } else {
          setAlertMessage(<FormattedMessage id="login.serverError" />);
          console.log(err);
        }
      });
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <AssignmentIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          <FormattedMessage id="landing.signUp" />
        </Typography>
        <form className={classes.form} noValidate>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                value={firstname}
                name="firstName"
                variant="outlined"
                required
                fullWidth
                id="firstName"
                onChange={handleFirstnameChange}
                label={intl.formatMessage({
                  id: "login.firstName",
                })}
                autoFocus
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                value={lastname}
                onChange={handleLastnameChange}
                fullWidth
                id="lastName"
                label={intl.formatMessage({
                  id: "login.lastName",
                })}
                name="lastName"
                autoComplete="lname"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                onChange={handleEmailChange}
                value={email}
                id="email"
                label={intl.formatMessage({
                  id: "login.email",
                })}
                name="email"
                autoComplete="email"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="password"
                onChange={handlePasswordChange}
                value={password}
                label={intl.formatMessage({
                  id: "login.password",
                })}
                type="password"
                id="password"
                autoComplete="current-password"
              />
            </Grid>
          </Grid>
          <Button
            type="button"
            fullWidth
            variant="contained"
            color="primary"
            onClick={handleSignUp}
            className={classes.submit}
          >
            <FormattedMessage id="landing.signUp" />
          </Button>

          <Grid container justify="flex-end">
            <Grid item>
              <p style={{ color: "red" }}>{alertMessage}</p>
            </Grid>
          </Grid>
          <Grid container justify="flex-end">
            <Grid item>
              <Link href="/signin" variant="body2">
                <FormattedMessage id="login.alreadyHaveAccount" />
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Footer />
    </Container>
  );
};

export default injectIntl(SignUp);
