/* eslint-disable react/prop-types */
import React, { Component } from "react";

import classes from "./AddMember.module.css";
import Layout from "../../../components/Layout/Layout";
import Footer from "../../../components/Footer/Footer";
import Member from "../../../components/Member/Member";
import { UserRoles } from "../../../common/UserRoles";

import Grid from "@material-ui/core/Grid";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import Button from "@material-ui/core/Button";
import Modal from "../../../components/Modal/Modal";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";

import axios from "axios";
import { FormattedMessage, injectIntl } from "react-intl";

class AddMember extends Component {
  constructor(props) {
    super(props);
    this.state = {
      addNewMemeber: false,
      newMemeber: {
        role: "",
        email: "",
      },
      curList: [{ role: "", email: "" }],
    };
    this.closeAddMemberModal = this.closeAddMemberModal.bind(this);
    this.handleRoleChange = this.handleRoleChange.bind(this);
    this.updateNewMember = this.updateNewMember.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  showAddMemberModal() {
    this.setState({ addNewMemeber: true });
  }

  closeAddMemberModal() {
    this.setState({ addNewMemeber: false });
  }

  updateNewMember() {
    console.log("###After Modal: " + JSON.stringify(this.state.newMemeber));
    let postData = {
      role: "",
      email: "",
      workspaceId: "2e2f20e0-8300-4695-9b79-56d8a6fa5622",
    };

    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    postData.email = this.state.newMemeber.email;
    postData.role = this.state.newMemeber.role;
    // TODO: remove mock endpoint
    const endpoint = "localhost:8080/workspace/adduser";
    axios.post(endpoint, postData, config).then((res) => {
      console.log(res);
    });

    console.log("POST DATA: " + JSON.stringify(postData));
    this.setState({
      addNewMemeber: false,
      curList: [...this.state.curList, this.state.newMemeber],
    });
  }

  handleChange(event) {
    const value = event.target.value;
    this.setState({
      ...this.state,
      [event.target.name]: value,
      newMemeber: {
        role: this.state.newMemeber.role,
        email: event.target.value,
      },
    });
  }

  handleRoleChange(role) {
    this.setState({
      newMemeber: {
        role: role,
        email: this.state.newMemeber.email,
      },
    });
  }

  render() {
    return (
      <Layout rolename="admin">
        <Grid
          style={{
            marginTop: 80,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          {/* title area */}
          <div className={classes.TitleAndSearch}>
            <div className={classes.Title}>
              <FormattedMessage id="admin.team"></FormattedMessage>
            </div>
          </div>

          {/* button and search area */}
          <Grid
            container
            direction="row"
            justify="space-between"
            spacing={0}
            item
            xs={12}
          >
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={4}
            >
              <Button
                variant="contained"
                startIcon={<AccountCircleIcon />}
                type="submit"
                value="Submit"
                onClick={() => {
                  this.showAddMemberModal();
                }}
              >
                <FormattedMessage id="admin.invite"></FormattedMessage>
              </Button>
            </Grid>
          </Grid>

          {/* members lists area */}
          <Grid
            container
            direction="row"
            justify="space-evenly"
            spacing={0}
            style={{
              height: 700,
              background: "#5D5D5B",
              borderRadius: 10,
              color: "#5D5D5B",
            }}
            item
            xs={12}
          >
            {/* admin members area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.MemberTitle}>
                <FormattedMessage id="admin.admin"></FormattedMessage>
              </div>
              {this.state.curList.map((item) => {
                if (item.role === UserRoles.ADMIN)
                  return <Member key={item.email} membername={item.email} />;
              })}
            </Grid>
            {/* designer members area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.MemberTitle}>
                <FormattedMessage id="admin.designer"></FormattedMessage>
              </div>
              {this.state.curList.map((item) => {
                if (item.role === UserRoles.DESIGNER)
                  return <Member key={item.email} membername={item.email} />;
              })}
            </Grid>
            {/* developer members area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                marginRight: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.MemberTitle}>
                <FormattedMessage id="admin.developer"></FormattedMessage>
              </div>
              {this.state.curList.map((item) => {
                if (item.role === UserRoles.DEVELOPER)
                  return <Member key={item.email} membername={item.email} />;
              })}
            </Grid>
          </Grid>
        </Grid>
        <Modal
          show={this.state.addNewMemeber}
          modalClosed={this.closeAddMemberModal}
        >
          <form className={classes.form} noValidate>
            <div className={classes.ModalTitle}>
              <FormattedMessage id="admin.memberInfo"></FormattedMessage>
            </div>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="email"
                  label={this.props.intl.formatMessage({
                    id: "admin.email",
                  })}
                  name="email"
                  value={this.state.newMemeber.email}
                  onChange={this.handleChange}
                  autoComplete="email"
                />
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <TextField
                select
                fullWidth
                label={this.props.intl.formatMessage({
                  id: "admin.role",
                })}
                // autoComplete={this.state.newMemeber.role}
                required
                helperText={this.props.intl.formatMessage({
                  id: "admin.selectRole",
                })}
                style={{ marginTop: 10 }}
              >
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "admin.admin",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.ADMIN)}
                >
                  <FormattedMessage id="admin.admin"></FormattedMessage>
                </MenuItem>
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "admin.designer",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.DESIGNER)}
                >
                  <FormattedMessage id="admin.designer"></FormattedMessage>
                </MenuItem>
                <MenuItem
                  value={this.props.intl.formatMessage({
                    id: "admin.developer",
                  })}
                  onClick={() => this.handleRoleChange(UserRoles.DEVELOPER)}
                >
                  <FormattedMessage id="admin.developer"></FormattedMessage>
                </MenuItem>
              </TextField>
            </Grid>
            <Button
              fullWidth
              variant="contained"
              color="primary"
              style={{
                marginTop: 10,
                marginBottom: 30,
              }}
              onClick={() => this.updateNewMember()}
            >
              <FormattedMessage id="admin.inviteButton"></FormattedMessage>
            </Button>
          </form>
        </Modal>
        <Footer />
      </Layout>
    );
  }
}

export default injectIntl(AddMember);
