import * as actionTypes from "./actions";

const initialState = {
  // initial task
  currentTask: {},
  allNewTasks: [],
  // initial language
  currentLanguage: "en",
  // initial workspace
  currentWorkspace: {},
  allNewWorkspaces: [],
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    // update current task
    case actionTypes.UPDATE_CURRENT_TASK:
      return {
        ...state,
        currentTask: action.data,
      };
    // update all new tasks
    case actionTypes.UPDATE_ALL_NEWTASKS:
      return {
        ...state,
        allNewTasks: action.data,
      };
    // update current workspace
    case actionTypes.UPDATE_CURRENT_WORKSPACE:
      return {
        ...state,
        currentWorkspace: action.data,
      };
    // update all new workspaces
    case actionTypes.UPDATE_ALL_NEWWORKSPACES:
      return {
        ...state,
        allNewWorkspaces: action.data,
      };
    // update language
    case actionTypes.UPDATE_LANGUAGE:
      return {
        ...state,
        currentLanguage: action.data,
      };
    default:
      return state;
  }
};

export default reducer;
