import React from "react";
import { Component } from "react";
import PropTypes from "prop-types";
import { BPMN_SERVER_URL } from "../../common/Url";
import axios from "axios";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import ReactBpmn from "../../components/BpmnJs/BpmnViewer";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import { TaskStatus } from "../../common/TaskStatus";
import { FormattedMessage, injectIntl } from "react-intl";
import "./DeployModeDialog.css";

class DeployModeDialog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      bearerToken: localStorage.getItem("token"),
      open: false,
      workspaceId: props.workspaceId,
      workflowId: props.workflowId,
      diagramXML: "",
      tasks: [],
      deployable: false,
      intl: props.intl,
    };

    this.deployWorkflow = this.deployWorkflow.bind(this);
    this.handleClickOpen = this.handleClickOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  async handleClickOpen() {
    const config = {
      headers: { Authorization: `Bearer ${this.state.bearerToken}` },
    };

    // eslint-disable-next-line react/prop-types
    const requestURL =
      BPMN_SERVER_URL +
      "/" +
      this.state.workspaceId +
      "/" +
      this.state.workflowId;

    axios
      .get(requestURL, config)
      .then(async (res) => {
        const bpmnDiagramJson = res.data;
        if (bpmnDiagramJson && bpmnDiagramJson.diagram) {
          const decodedXml = window.atob(bpmnDiagramJson.diagram);
          this.setState({
            diagramXML: decodedXml,
            tasks: bpmnDiagramJson.tasks,
          });

          var allTaskPassed = true;
          if (
            bpmnDiagramJson.tasks == undefined ||
            bpmnDiagramJson.tasks.length == 0
          ) {
            allTaskPassed = false;
          } else {
            bpmnDiagramJson.tasks.forEach((task) => {
              if (task.status !== TaskStatus.PASSED) {
                allTaskPassed = false;
              }
            });
          }
          this.setState({ deployable: allTaskPassed });
        }
        this.setState({
          open: true,
        });
      })
      .catch((error) => {
        console.error(error);
      });
  }

  handleClose() {
    this.setState({
      open: false,
    });
  }

  /**
   * Call the deploy api
   */
  deployWorkflow() {
    const config = {
      headers: { Authorization: `Bearer ${this.state.bearerToken}` },
    };

    axios
      .put(
        BPMN_SERVER_URL +
          "/" +
          this.state.workspaceId +
          "/" +
          this.state.workflowId +
          "/deployment",
        config
      )
      .then((res) => {
        if (res.data === "success") {
          // TODO
          console.log("success");
        } else {
          // TODO
        }
      })
      .catch((error) => {
        this.openAlertbar("error", "An error occurs when deploying");
        console.error(error);
      });
  }

  render() {
    return (
      <div>
        <Button
          variant="contained"
          startIcon={<CloudUploadIcon />}
          onClick={this.handleClickOpen}
        >
          <FormattedMessage
            id="designer.button.deploy"
            defaultMessage="Deploy"
          />
        </Button>
        <Dialog
          onClose={this.handleClose}
          aria-labelledby="simple-dialog-title"
          open={this.state.open}
          maxWidth="md"
          fullWidth
        >
          <DialogTitle id="simple-dialog-title">Deploy Mode</DialogTitle>
          <ReactBpmn
            diagramXML={this.state.diagramXML}
            tasks={this.state.tasks}
          />
          <Button
            variant="contained"
            onClick={this.deployWorkflow}
            disabled={!this.state.deployable}
          >
            <FormattedMessage
              id="designer.button.deploy"
              defaultMessage="Deploy"
            />
          </Button>
        </Dialog>
      </div>
    );
  }
}

DeployModeDialog.propTypes = {
  workspaceId: PropTypes.string.isRequired,
  workflowId: PropTypes.string.isRequired,
  intl: PropTypes.object, // injectIntl will do that
};

export default injectIntl(DeployModeDialog);
