/* eslint-disable react/prop-types */
import React, { Component } from "react";

import classes from "./Details.module.css";
import Layout from "../../components/Layout/Layout";
import Footer from "../../components/Footer/Footer";
import Card from "../../components/Card/CardBar";
import Menu from "../../components/Menu/MenuBar";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";

import { connect } from "react-redux";
import * as actionTypes from "../../store/actions";
import { FormattedMessage } from "react-intl";

class Description extends Component {
  constructor(props) {
    super(props);
    this.state = {
      curTask: {},
    };
  }

  render() {
    return (
      <Layout rolename="developer">
        <Grid
          style={{
            marginTop: 20,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          <div className={classes.Title}>
            <Grid
              style={{
                textAlign: "center",
              }}
              item
              xs
              container
              direction="column"
              spacing={4}
            >
              <Grid item xs>
                <Typography gutterBottom variant="h4" color="initial">
                  <FormattedMessage id="developer.task.name" />
                  {this.props.curtask.name}
                </Typography>
                <Typography variant="h6" color="textSecondary">
                  <FormattedMessage id="developer.task.bpmnid" />
                  {this.props.curtask.bpmnTaskId}
                </Typography>
              </Grid>
            </Grid>
          </div>

          <Grid
            direction="row"
            justify="space-between"
            alignItems="flex-start"
            spacing={0}
            style={{
              marginTop: 40,
            }}
            container
            item
            xs={12}
          >
            <Grid
              style={{
                height: 260,
                background: "#354868",
                borderRadius: 10,
              }}
              item
              xs={2.5}
            >
              <Menu className={classes.Menu} />
            </Grid>

            <Grid
              style={{
                height: 600,
                background: "#5D5D5B",
                borderRadius: 10,
                color: "#5D5D5B",
              }}
              item
              xs={9}
            >
              <Grid
                style={{
                  marginTop: 20,
                  marginLeft: 20,
                  marginRight: 20,
                  height: 530,
                  background: "#C4C4C4",
                  borderColor: "white",
                  border: 0,
                  borderRadius: 10,
                  overflow: "scroll",
                }}
                item
                xs={12}
              >
                <div className={classes.SubTitle}>
                  <FormattedMessage id="developer.task.description" />
                </div>
                <Card
                  className={classes.Card}
                  cardname={this.props.curtask.description}
                />
                <div className={classes.SubTitle}>
                  <FormattedMessage id="developer.task.input" />
                </div>
                <Card
                  className={classes.Card}
                  cardname={JSON.stringify(this.props.curtask.input)}
                />
                <div className={classes.SubTitle}>
                  <FormattedMessage id="developer.task.output" />{" "}
                </div>
                <Card
                  className={classes.Card}
                  cardname={JSON.stringify(this.props.curtask.output)}
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        {/* footer */}
        <Footer />
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    curtask: state.currentTask,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentTask: (currentTask) =>
      dispatch({ type: actionTypes.UPDATE_CURRENT_TASK, data: currentTask }),
    updateAllTasks: (allTask) =>
      dispatch({ type: actionTypes.UPDATE_ALL_NEWTASKS, data: allTask }),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Description);
