/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import { is } from "bpmn-js/lib/util/ModelUtil";

import React, { Component } from "react";

import { v4 as uuidv4 } from "uuid";
import "./BpmnPropertiesPanel.css";

import { DEVELOPER_SERVER_URL } from "../../common/Url";
import axios from "axios";

// Mock endpoints
let DEVELOPERS_LIST_ENDPOINT =
  DEVELOPER_SERVER_URL + "/89600b7e-7cf0-11eb-9439-0242ac130002";

export default class BpmnPropertiesPanel extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedElements: [],
      element: null,
      developers: [],
      developersIsLoaded: false,
      error: null,
    };
  }

  componentDidMount() {
    const { modeler } = this.props;

    modeler.on("selection.changed", (e) => {
      const { element } = this.state;

      this.setState({
        selectedElements: e.newSelection,
        element: e.newSelection[0],
        developers: this.state.developers,
      });
    });

    modeler.on("element.changed", (e) => {
      console.log(e);
      const { element } = e;
      const { element: currentElement } = this.state;
      if (!currentElement) {
        return;
      }
      // update panel, if currently selected element changed
      if (element.id === currentElement.id) {
        this.setState({
          element: element,
        });
      }
    });

    this.loadDevelpoers(DEVELOPERS_LIST_ENDPOINT);
  }

  /**
   * Loading developer list and storing it in state
   * @param {*} endpoint the get developers list api
   */
  async loadDevelpoers(endpoint) {
    const config = {
      headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
    };

    const developerList = await axios
      .get(endpoint, config)
      .then((res) => res.data);

    this.setState({
      developersIsLoaded: true,
      developers: developerList,
    });
  }

  render() {
    const { modeler } = this.props;
    const { selectedElements, element } = this.state;
    return (
      <div>
        {selectedElements.length === 1 && (
          <ElementProperties
            modeler={modeler}
            element={element}
            developers={this.state.developers}
          />
        )}
      </div>
    );
  }
}

function ElementProperties(props) {
  let { element, modeler, developers } = props;

  if (element.labelTarget) {
    element = element.labelTarget;
  }

  /**
   * When picking up a developer, this function updates the task/developer's info in the xml
   * @param {*} target
   */
  function developerIsSelected(target) {
    const modeling = modeler.get("modeling");
    const selectedIndex = target.selectedIndex - 1;

    let taskId = element.businessObject.get("waterflow:task_id");

    if (!taskId) {
      taskId = uuidv4();
    }

    modeling.updateProperties(element, {
      "waterflow:task_id": taskId,
      "waterflow:developer_id": developers[selectedIndex].id,
      "waterflow:developer_name": developers[selectedIndex].name,
    });
  }
  return (
    <div className="element-properties" key={element.id}>
      {is(element, "bpmn:Task") && (
        <fieldset>
          <label>Assign a developer</label>

          <select
            value={element.businessObject.get("waterflow:developer_name")}
            onChange={(event) => {
              developerIsSelected(event.target);
            }}
          >
            <option>Choose Dvelopers</option>
            {developers.map((data) => (
              <option key={data.id}>{data.name}</option>
            ))}
          </select>
        </fieldset>
      )}
    </div>
  );
}
