import React from "react";
import classes from "./WorkspaceBar.module.css";

import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";

import { FormattedMessage } from "react-intl";

const workspacebar = (props) => {
  const useStyles = makeStyles({
    root: {
      marginRight: "10px",
      background: "#354868",
      borderRadius: 3,
      border: 0,
      color: "white",
      height: 40,
      padding: "0 10px",
      boxShadow: "0 3px 5px 2px rgba(137, 136, 128, .3)",
    },
    label: {
      textTransform: "capitalize",
    },
  });
  const mclasses = useStyles();
  return (
    <Grid
      container
      spacing={0}
      direction="row"
      justify="flex-start"
      alignItems="center"
      style={{
        minWidth: 30,
        height: 80,
        background: "white",
        marginLeft: 15,
        marginTop: 10,
        borderRadius: 3,
      }}
      item
      xs={11}
    >
      <Grid item xs={8} className={classes.MainArea}>
        <div className={classes.WorkspaceName}>{props.workspacename}</div>
      </Grid>

      <Grid item xs={3}>
        <Button
          onClick={(item) => props.clicked(item)}
          classes={{
            root: mclasses.root,
            label: mclasses.label,
          }}
        >
          <FormattedMessage id="workspaces.enterWorkspace" />
        </Button>
      </Grid>
    </Grid>
  );
};

export default workspacebar;
