/* eslint-disable react/prop-types */
import React, { Component } from "react";

import classes from "./AllTasks.module.css";
import Task from "../../components/Task/TaskBar";
import Layout from "../../components/Layout/Layout";
import Footer from "../../components/Footer/Footer";
import { DEVELOPER_SERVER_URL, DEVELOPER_DETAILS_URL } from "../../common/Url";
import { TaskStatus } from "../../common/TaskStatus";

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import SearchIcon from "@material-ui/icons/Search";

import axios from "axios";
import { connect } from "react-redux";
import * as actionTypes from "../../store/actions";
import { FormattedMessage, injectIntl } from "react-intl";

class AllTasks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      newTask: [],
      filter: "",
    };
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    const developerId = "b9163db6-f71c-4b0f-826d-fb3a209065c5";
    const url = DEVELOPER_SERVER_URL + "/" + developerId + "/tasks";
    axios
      .get(url)
      .then((response) => response.data)
      .then((data) => {
        this.props.updateAllTasks(data);
        console.log(data);
        console.log("##############################check state ######");
        console.log(this.props.newtasks);
      });
  }

  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  directToTask(data) {
    this.props.updateCurrentTask(data);
    this.props.history.push(DEVELOPER_DETAILS_URL);
  }

  render() {
    return (
      <Layout rolename="developer">
        <Grid
          style={{
            marginTop: 80,
            marginLeft: 100,
            marginRight: 100,
          }}
          item
          xs={12}
        >
          {/* title area */}
          <div className={classes.TitleAndSearch}>
            <div className={classes.Title}>
              <FormattedMessage id="developer.allTasks"></FormattedMessage>
            </div>
          </div>

          {/* search area */}
          <Grid
            container
            direction="row"
            justify="flex-end"
            spacing={0}
            item
            xs={12}
          >
            <Grid
              style={{
                marginTop: 20,
                marginBottom: 20,
              }}
              item
              xs={6}
            >
              <TextField
                style={{
                  display: "flex",
                  borderColor: "white",
                  border: 0,
                  borderRadius: 3,
                  backgroundColor: "white",
                  paddingLeft: 10,
                  paddingRight: 10,
                }}
                placeholder={this.props.intl.formatMessage({
                  id: "developer.searchTask",
                })}
                variant="standard"
                name="filter"
                value={this.state.filter}
                onChange={this.handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment>
                      <IconButton>
                        <SearchIcon />
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
          </Grid>

          {/* all tasks area */}
          <Grid
            container
            direction="row"
            justify="space-evenly"
            spacing={0}
            style={{
              height: 700,
              background: "#5D5D5B",
              borderRadius: 10,
              color: "#5D5D5B",
            }}
            item
            xs={12}
          >
            {/* NEW task area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.TaskTitle}>
                <FormattedMessage id="developer.newTasks"></FormattedMessage>
              </div>
              {this.props.newtasks.map((item) => {
                if (
                  item.status === TaskStatus.NEW &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Task
                      key={item.id}
                      className={classes.Task}
                      taskname={item.name}
                      clicked={() => this.directToTask(item)}
                    />
                  );
              })}
            </Grid>
            {/* DEPLOYED task area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.TaskTitle}>
                <FormattedMessage id="developer.deployedTask"></FormattedMessage>
              </div>
              {this.props.newtasks.map((item) => {
                if (
                  item.status === TaskStatus.PASSED &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Task
                      key={item.id}
                      className={classes.Task}
                      taskname={item.name}
                      clicked={() => this.directToTask(item)}
                    />
                  );
              })}
            </Grid>
            {/* FAILED task area */}
            <Grid
              style={{
                marginTop: 20,
                marginLeft: 20,
                marginRight: 20,
                height: 660,
                background: "#C4C4C4",
                borderColor: "white",
                border: 0,
                borderRadius: 10,
                overflow: "scroll",
              }}
              item
              xs={3}
            >
              <div className={classes.TaskTitle}>
                <FormattedMessage id="developer.failedTask"></FormattedMessage>
              </div>
              {this.props.newtasks.map((item) => {
                if (
                  item.status === TaskStatus.FAILED &&
                  item.name
                    .toLowerCase()
                    .includes(this.state.filter.toLowerCase())
                )
                  return (
                    <Task
                      key={item.id}
                      className={classes.Task}
                      taskname={item.name}
                      clicked={() => this.directToTask(item)}
                    />
                  );
              })}
            </Grid>
          </Grid>
        </Grid>
        {/* footer */}
        <Footer />
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    newtasks: state.allNewTasks,
    filter: state.filter,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCurrentTask: (currentTask) =>
      dispatch({ type: actionTypes.UPDATE_CURRENT_TASK, data: currentTask }),
    updateAllTasks: (allTask) =>
      dispatch({ type: actionTypes.UPDATE_ALL_NEWTASKS, data: allTask }),
  };
};

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(AllTasks)
);
